<template>
	<view class="footer">
		<view class="showTitle">
			<view class="vrshow" v-for="(item,index) in boottomList" :key='index' @click="show(index)">
				<view class="">
					<text class="iconfont" :class="item.icon"></text>
				</view>
				<text>
					{{item.title}}
				</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 底部导航
				boottomList:[
					// {icon:'iconfuzhusheji',title:'辅助设计'},
					{icon:'iconshebeiguanli',title:'XR显示'},
					{icon:'iconsanweixianshi',title:'三维显示'},
					// #ifdef APP-PLUS
					{icon:'iconzhuanfa',title:'病例转发'},
					// #endif
				],
			}
		},
		props:{
			stlId:{
				type:String,
				default:''
			}
		},
		methods:{
			show(index){
				console.log(index)
				if(index==0){//xr显示
					console.log('xr显示')
					// #ifdef APP-PLUS
						this.$emit('vrshow')
					// #endif
					// #ifdef H5
						uni.showToast({
							title:'网页端不能显示',
							icon:'none'
						})
					// #endif
					// #ifdef MP-WEIXIN
					this.$emit('vrshow')
					// #endif
				}else if(index==1){//三维显示
					console.log(this.stlId)
					this.Jsshow(this.stlId)
				}else if(index==2){//转发
					console.log('转发')
					this.$emit('caseshare')
					// #ifdef APP-PLUS
						this.$emit('caseshare')
					// #endif
					// #ifdef H5
						// uni.showToast({
						// 	title:'网页端不能分享',
						// 	icon:'none'
						// })
					// #endif
				}
			},
			// 三维显示
			Jsshow(stlId){
				console.log(stlId)
				if(!stlId){
					uni.showToast({
						title:'无相关文件',
						icon:'none'
					})
					return;
				}
				var baseUrl='https://technology.shoyii.com/?cos'+'='+true+'&userType=D'+'&token='+'&modelType='+'&titlename='+'&fileId='+stlId
				uni.navigateTo({
					url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+encodeURIComponent(baseUrl),
					animationType:'pop-in'
				})
			},
		},
	}
</script>

<style scoped>
	.footer{
		height:55px;
		border-top:2rpx solid #EEEEEE;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 30rpx;
		background: #FFFFFF;
	}
	.showTitle{
		display: flex;
		width: 100%;
		height: 80rpx; 
	}
	.vrshow{
		flex: 1;
		color: #000000;
		font-size: 20rpx;
		text-align: center;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.vrshow .iconfont{
		font-size: 36rpx;
	}
</style>
